import React from "react";
import Promo from "./Promo";
import News from "./News";
const Banner = (props) => {
  let html = "";
  let { variant } = props;
  switch (variant) {
    case "promo":
      html = <Promo />;
      break;
    case "news":
      html = <News />;
      break;
    default:
      html = <Promo />;
      break;
  }
  return <>{html}</>;
};

export default Banner;
